.wisski {
  padding: 1em;
}

.wisski h3 {
  padding: 0;
}

.wisski a.pure-button {
  float: right;
  position: relative;
  bottom: 1em;
}

.wisski.running {
  background-color: #9ada07;
}

.wisski.stopped {
  background-color: #ff7a7a;
}

.disabled {
  color: gray;
}

.overflow {
  overflow: auto;
}

.info-chip {
  text-align: center;
  border-radius: 5px;
  min-width: 75px;
  height: 15px;
  padding: 5px;
  display: block;
}

.info-chip.note {
  color: #fff;
  background-color: gray;
}

.info-chip.info {
  color: #fff;
  background-color: green;
}

.info-chip.warning {
  color: #000;
  background-color: #ff0;
}

.info-chip.error {
  color: #000;
  background-color: red;
}

